* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  color: #ffffff;
}

body {
  font-size: 18px;
  background-color: #111A28; 
}

.py-60 {
  padding: 60px 0;
}

h1 {
  font-size: 50px;
}

h2 {
  font-size: 40px;
}

h3 {
  font-size: 25px;
}

h4 {
  font-size: 20px;
}

img {
  width: 100%;
}

.container {
  max-width: 1100px;
  margin: 0px auto;
  padding: 0 12px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}

.grid > * {
  padding: 20px;
  background-color: #1D293A;
}

@media (max-width: 1199px) {
  .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 767px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 575px) {
  .grid {
    grid-template-columns: repeat(1, 1fr);
  }
}

.btn {
  font-size: 18px;
  padding: 10px 25px;
  background-color: #00CF5D;
  outline: 0;
  border: 0;
  cursor: pointer;
}