header {
	background-color: #1D293A;
	padding: 20px 0;
}

nav {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

nav .logo a {
	font-weight: 700;
}

nav .main-menu ul {
	text-align: right;
}

nav .main-menu ul li {
	display: inline-block;
}

nav .main-menu ul li:not(:last-child) {
	margin-right: 20px;
}

nav .main-menu ul li a:hover {
	color: #00CF5D;
}